import React, { Fragment, useState } from "react";
import { AppBar, SlickSlider } from "components";
import { HubItem } from "./components";
import { BrowserRouter as Router, Link } from "react-router-dom";

const Hub = props => {
    const [loading] = useState(false);

    if (!!loading) {
        return <Loading />;
    }

    return (
        <Fragment>
            <AppBar />
            <main>
                <br></br>
                <div className="button-group">
                    <Link className="button outline" to="/welcome">Welcome</Link>
                    <Link className="button outline" to="/hub">Hub</Link>
                    <Link className="button outline" to="/profile">Profile</Link>
                    <Link className="button outline" to="/education">Education</Link>
                    <Link className="button outline" to="/experience">Experience</Link>
                    <Link className="button outline" to="/projects">Projects</Link>
                </div>
            </main>
        </Fragment>
    );
};

export default Hub;
